@use '../../../_lib' as lib;

.search {
    display: flex;
    height: 10vh;
    margin-top: 1rem;
    justify-content: center;
    align-items: center;

    &__input {
        width: 30vw;
        font-size: 1.5rem;
        margin-right: 1rem;
        padding: .2vw;
    }

    &__btn-container {
        display: flex;
        gap: 1rem;

        .search__btn {
            padding: .3vw .5vw;
            font-size: 1.1rem;
            border-radius: 13px;
            background-color: transparent;
            border: 1px solid transparent;
            cursor: pointer;


            &--search {
                background-color: lib.$secondary-clr;
                transition: .3s;


                &:hover {
                    background-color: transparent;
                    border: 1px solid lib.$secondary-clr;
                }

                &:active {
                    background-color: rgba(0, 119, 255, 0.808);
                }
            }

            &--clean {
                background-color: transparent;
                border: 1px solid rgba(255, 0, 0, 0.562);
                transition: .3s;

                &:hover {
                    background-color: rgba(255, 0, 0, 0.562);
                    border: 1px solid transparent;
                }

                &:active {
                    background-color: red;
                }
            }
        }
    }

}